  /* Booking Form Style */


  .booking-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 600px;
    margin: 2rem auto;
    padding: 20px;
    border: solid thin whitesmoke;                           
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.9);
    background-color: rgba(201, 245, 241, 0); 
    z-index: 2; 
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: whitesmoke;
  }
  

  .form-group {
    margin-bottom: 15px;
    margin-right: 1.2rem;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-weight: bolder;
  }
  
  input::placeholder {
    font-weight: bolder;
    color: black;
  }
  
  .input,
  select {
    width: 100%;
    height: 2rem;
    padding: 8px;
    margin-bottom: 10px;
    border: solid thin whitesmoke;
    border-radius: 10px;
  }
  
  button {
    font-weight: bolder;
    /* width: 30%; */
    padding: 10px;
    /* background-color: #61a7ee; */
    color: whitesmoke;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
  }
  
  
  /* Container for check-in and check-out dates */
  .date-container {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
  
  .date-picker {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 10px;
    height: 2rem;
  }
  
  .react-datepicker__input-container input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 10px;
    height: 2rem;
  }
  
  /* Custom styles for react-datepicker */
  .react-datepicker {
    font-size: 14px;
    color: #007bff;
  }
  
  .react-datepicker__header {
    background-color: #007bff;
    border-bottom: none;
  }
  
  .react-datepicker__day-name, 
  .react-datepicker__day, 
  .react-datepicker__time-name {
    color: #333;
  }
  
  /* Background Image */
  .bg-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50vh; 
    z-index: -1;
    /* background: url('https://cdn.pixabay.com/photo/2021/04/17/04/56/tv-cabinet-6185022_1280.jpg') no-repeat center center; */
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  button:hover {
    background-color: #348ae6;
    color: whitesmoke;
  }
  
  /* Media Queries for smaller screens */
  @media (max-width: 768px) {
    .booking-form {
      padding: 10px;
    }
  
    input,
    select {
      padding: 6px;
    }
  
    button {
      padding: 8px;
    }
  }